.divForm {
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 200px;
    gap: 10px;
    &__label {
        font-size: 20px;
        color: #8a5a44;
      }
      &__input {
        width: 300px;
      }
      button {
        min-width: 130px;
        height: 30px;
        color: #fff;
        padding: 5px 10px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.3s ease;
        position: relative;
        display: inline-block;
        outline: none;
        border-radius: 20px;
        border: 2px solid #495057;
        background: #495057;
      }
      button:hover {
        background: #fff;
        color: #495057;
      }
  }
  .home__button{
    padding-top: 200px;
  }
}
